<template>
  <div class="user-box">
    <HeadTop title="IDO" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <!-- <div class="nft-box">
      <div class="title"> {{$t('index.加入合伙人获取元素世界勋章NFT')}}</div>
      <div class="desc">
         {{$t('index.直接邀请至少个用户', {introNum: introNum, buyNftAmount: buyNftAmount})}}
      </div>
      <div class="btn-box">
         <van-button :disabled="isBuyNft" v-if="Number(usdtAllowance) > 0" type="primary" size="large" @click="buyNft"> {{$t('index.购买NFT')}}</van-button>
          <van-button v-else type="primary" size="large" @click="approveUsdt"> {{$t('index.授权')}} USDT</van-button>
      </div>
      <div class="tip"> {{$t('index.花费USDT获得NFT', {buyNftAmount, introNum})}}</div>
    </div> -->
    <div class="nft-box">
      <div class="title">IDO</div>
       <div class="desc">
         {{$t('index.花费USDT获得$ELE', {idoAmount: idoAmount, num: idoAmount})}}
      </div>
      <div class="btn-box">
         <van-button :disabled="isIdo" :loading="btn.loading" v-if="Number(usdtAllowance) > 0" type="primary" size="large" @click="ido">IDO</van-button>
         <van-button v-else :loading="btn.loading" type="primary" size="large" @click="approveUsdt"> {{$t('index.授权')}} USDT</van-button>
      </div>
    </div>
    <div class="tip-desc">
      {{$t('index.已认购的IDO份额')}}:{{idoNum}}
    </div>
    <div class="tip-desc">
      {{$t('index.已领取NFT勋章数量')}}:{{nftNum}}
    </div>
  </div>  
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import config from "@/utils/config";
import idoAbi from "@/utils/idoAbi";
import BigNumber from 'bignumber.js';
import tokenAbi from "@/utils/abi";
import { intro } from '@/storage'
import { useI18n } from "vue-i18n"
import { MaxUint256 } from "@ethersproject/constants";
import { Toast } from 'vant';
import nftAbi from "@/utils/nftAbi";

export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      idoContract: null,
      usdtContract: null,
      nftContract: null,
      usdtBalance: '0.00',
      idoAmount: '0.00',
      buyNftAmount: '0.00',
      usdtAllowance: '',
      introNum: 0,
      intro: intro.get() || config.rootAddress,
      isBuyNft: false,
      isGetNft: false,
      isIdo: false,
      nftNum: 0,
      idoNum: 0,
      btn: {
        loading: false
      },
      initContract: () => {
        data.idoContract = new data.web3.eth.Contract(idoAbi, config.idoAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.nftContract = new data.web3.eth.Contract(nftAbi, config.nftAddress)
        data.initData()
      },
      initData: () => {
        data.getBalance()
        data.getIdoInfo()
        data.getUsdtAllowance()
        data.getUserInfo()
      },
      getUserInfo: async () => {
        let userInfo = await data.idoContract.methods.userInfo(data.address).call()
        data.isBuyNft = userInfo.isBuyNft
        data.isGetNft = userInfo.isGetNft
        data.isIdo = userInfo.isIdo
      },
       getBalance: async () => {
        let usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call()
        data.usdtBalance =  new BigNumber(usdtBalance).dividedBy(10 ** 18).toFixed(2,1)
      },
      getIdoInfo: async () => {
        let idoAmount = await data.idoContract.methods.idoAmount().call()
        let buyNftAmount = await data.idoContract.methods.buyNftAmount().call()
        let introNum = await data.idoContract.methods.introNum().call()
        data.idoAmount =  new BigNumber(idoAmount).dividedBy(10 ** 18).toFixed(2,1)
        data.buyNftAmount =  new BigNumber(buyNftAmount).dividedBy(10 ** 18).toFixed(2,1)
        data.introNum = introNum
        data.nftNum = await data.idoContract.methods.total().call()
        data.idoNum = await data.idoContract.methods.idoNum().call()
      },
      getUsdtAllowance: async () => {
        let result = await data.usdtContract.methods.allowance(data.address, config.idoAddress).call();
        data.usdtAllowance = result;
      },
      approveUsdt: async () => {
         data.btn.loading = true
        try {
          await data.usdtContract.methods.approve(config.idoAddress, MaxUint256).send({ from: data.address });
          data.getUsdtAllowance()
           data.btn.loading = false
        } catch (error) {
           data.btn.loading = false
        }
      },
      buyNft: async () => {
        let userInfo = await data.idoContract.methods.userInfo(data.address).call()
       
        if(userInfo.isBuyNft) {
           Toast('此地址已经购买过了')
          return
        }
        if (Number(data.usdtBalance) < Number(data.buyNftAmount)) {
          Toast(t('index.USDT余额不足'))
          return
        }
         data.btn.loading = true
        try {
          await data.idoContract.methods.buyNft().send({ from: data.address });
           data.btn.loading = false
          Toast(t('index.操作成功'))
          data.initData()
        } catch (error) {
          data.btn.loading = false
        }
      },
       ido: async () => {
        let userInfo = await data.idoContract.methods.userInfo(data.address).call()
       
        if(userInfo.isIdo) {
           Toast('此地址已经IDO过了')
          return
        }
        if (Number(data.usdtBalance) < Number(data.idoAmount)) {
          Toast(t('index.USDT余额不足'))
          return
        }
         data.btn.loading = true
        try {
          await data.idoContract.methods.ido(data.intro).send({ from: data.address });
           data.btn.loading = false
          Toast(t('index.操作成功'))
          data.initData()
        } catch (error) {
           data.btn.loading = false
          console.log(error)
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.tip-desc{
  color: rgba(255, 255, 255, 0.8);
  line-height: 30px;
}
.user-box{
  padding: 20px 15px 0;
}
.nft-box{
  padding: 20px;
  margin-bottom: 15px;
  background: linear-gradient(0deg, rgba(34, 37, 46, 0.64) 1%, rgba(34, 37, 46, 0.72) 100%);
  border: 1px solid rgba(101, 255, 0, 0.4);
  border-radius: 10px;
  .title{
    font-size: 18px;
    color: #a3ed3e;
    margin-bottom: 10px;
  }
  .desc{
    font-size: 15px;
    color: #fff;
    margin-bottom: 20px;
    word-break: break-all;
  }
  .tip{
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px;
    margin-top: 15px;
  }
}
</style>
